import ViewBook1 from "./ViewBook1";
import more from "../images/mainImages/showMore.png";
import left from '../images/mainImages/left.png';
import right from '../images/mainImages/right.png';
import {useEffect, useState} from "react";

function ListView(props) {
    const [isOver, setOver] = useState(false); //      
    const [index, setIndex] = useState(1); //      
    const [books, setBooks] = useState([]); // api   index    


    const mouseLeave = () => {
        return setOver(false);
    }

    const mouseOver = () => {
        return setOver(true);
    }

    const clickRight = () => {
        setIndex(index + 1);
        // 5  
        //  5       
        if (index === props.size / 5) {
            setIndex(1);
        }
        loanDate();
    }

    const clickLeft = () => {
        setIndex(index - 1);
        // 5  
        //  1       
        if (index === 1) {
            setIndex(props.size / 5);
        }
        loanDate();
    }

    //         
    const loanDate = (forward) => {
        const today = new Date();
        const lastDay = new Date(today.setDate(today.getDate() - forward));
        const year = lastDay.getFullYear();
        let month = lastDay.getMonth() + 1;
        let day = lastDay.getDate();

        month = month < 10 ? '0' + month : month;
        day = day < 10 ? '0' + day : day;

        return `${year}-${month}-${day}`;
    }
    // Main1  api  index   .
    // index   .
    useEffect(() => {
        const books = [];
        // index   0~4, 5~9, 10~14 ...    
        for (let i = index * 5 - 5; i < index * 5 ; i++) {
            books.push(props.books[i]);
        }
        setBooks(books);
    }, [index])

    return (
        <div style={bodyStyle} onMouseLeave={mouseLeave} onMouseOver={mouseOver}>
            <div style={{display: "flex", alignItems: "center"}}>
                <img src={props.icon} alt={props.icon} style={iconStyle}/>
                {   //    index  1, 2, 3, 4, 5... 
                    props.theme.indexOf('') === -1 &&
                    <div style={{fontFamily: "TAE"}}>{props.theme} ({index}/{props.size / 5})</div>
                }
                {
                    props.theme.indexOf('') >= 0 &&
                    <div style={{fontFamily: "TAE"}}>{props.theme} ({loanDate(index)})</div>
                }
            </div>
            <div style={booksStyle}>
            {
                // useState  books        
                books.map((book, index) => (
                    <ViewBook1 key={index} url={book.url} title={book.title} auth={book.author}/>
                ))
            }
            </div>
            <img src={more} alt={'more'} style={moreStyle} onClick={props.clickMore}/>
            {
                //  ListView       
                isOver &&
                <>
                    <img src={left} alt={'left'} style={leftStyle} onClick={clickLeft}/>
                    <img src={right} alt={'right'} style={rightStyle} onClick={clickRight}/>
                </>
            }
        </div>
    )
}

const bodyStyle = {
    width: '100%',
    height: '100%',
    position: "relative"
}

const booksStyle = {
    width: '100%',
    height: '90%',
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'space-around'
}

const moreStyle = {
    width: '2rem',
    height: '2rem',
    position: 'absolute',
    top: "0",
    right: "0"
}

const iconStyle = {
    width: '1.8rem',
    height: '1.8rem'
}

const leftStyle = {
    width: '2rem',
    height: '2rem',
    position: 'absolute',
    top: '50%',
    left: '0',
    backgroundColor: 'rgba(255, 255, 255, 0.9)'
}

const rightStyle = {
    width: '2rem',
    height: '2rem',
    position: 'absolute',
    top: '50%',
    right: '0',
    backgroundColor: 'rgba(255, 255, 255, 0.9)'
}

export default ListView;