import axios from "axios";

export async function fetchFavoriteAddAPI(bookId) {
    try {
        const response = await axios.post(`/favorite/${bookId}`, {}, { withCredentials: true });

        if (response.status === 200) {
            console.log('  ');
        }
    } catch (error) {
        console.error('  ', error);
    }
}
