import loginCss from '../styles/Login.module.css';
import logo from '../images/logo.png';
import { useState } from 'react';
import enter from '../images/enter.png';
import {useNavigate} from "react-router-dom";
import {useDispatch, useSelector} from "react-redux";
import {setLogin} from "../redux/slice/loginSlice";
import hide from '../images/join/hide.png';
import view from '../images/join/view.png';

function LoginPage() {
    const navigate = useNavigate(); //      navigate
    const [isFocus, setFocus] = useState(false); // input     
    const [Email, setEmail] = useState(null); //
    const [isShowPwd, setIsShowPwd] = useState(false); //    
    // redux 
    // login   login  
    const dispatch = useDispatch();
    const login = () => {
        dispatch(setLogin());
        return navigate('/');
    }

    // input      .
    // ,      .
    const checkValue = (e) => {
        if (e.target.value === '') {
            setFocus(false);
        } else {
            setFocus(true);
        }
    }
    //     
    const pressEnter = (e) => {
        if (e.key === 'Enter') {
            checkLogin();
        }
    }
    // DB    
    const checkLogin = () => {
        if (Email === '1234') {
            return login();
        }
        return alert('  ');
    }

    const showPwd = () => {
        setIsShowPwd(!isShowPwd);
    }

    //  
    const goJoin = () => {
        navigate('/join');
    }

    return (
        <div className={loginCss.body}>
            <div className={loginCss.loginBox}>
                <div className={loginCss.logoBox}>
                    <img src={logo} alt='logoImg'/>
                    <div></div>
                </div>
                <div className={loginCss.inputBox}>
                    <p style={{opacity: isFocus ? '0.6' : '0'}}> </p>
                    <input className={isFocus ? loginCss.focusInput : ''} type='text' placeholder=' .'
                           onFocus={() => setFocus(true)} onBlur={checkValue} onKeyPress={pressEnter}
                           onChange={(e) => setEmail(e.target.value)}></input>
                    <img src={enter} alt='' style={{opacity: isFocus ? '1' : '0'}} onClick={checkLogin}/>
                </div>
                <div className={loginCss.inputBox}>
                    <p style={{opacity: isFocus ? '0.6' : '0'}}> </p>
                    <input className={isFocus ? loginCss.focusInput : ''} type={isShowPwd ? 'text' : 'password'}
                           placeholder=' .'
                           onFocus={() => setFocus(true)} onBlur={checkValue} onKeyPress={pressEnter}
                           onChange={(e) => setEmail(e.target.value)}></input>
                    <img src={isShowPwd ? hide : view} alt='' style={{opacity: isFocus ? '1' : '0'}}
                         onClick={showPwd}/>
                </div>
                <div className={loginCss.buttonBox}>
                    <div className={loginCss.planeText}> ?</div>
                    <div className={`${loginCss.button}`} onClick={goJoin}></div>
                </div>
                <div className={loginCss.buttonBox}>
                    <div className={`${loginCss.button}`} onClick={() => window.open('/find/id', '_blank', 'width=700, height=200')}> </div>
                    <div className={`${loginCss.button}`} onClick={() => window.open('/find/pwd', '_blank', 'width=700, height=200')}> </div>
                </div>

            </div>
        </div>
    )
}

export default LoginPage;