import axios from "axios";

export async function detailAPI(isbn, config) {
    try {
        console.log("  ");
        const response = await axios.get(`/api/books/${isbn}`, {params: config});
        console.log("  ");
        console.log(response.data.response);

        return response.data.response;
    } catch (error) {
        console.log("    ");
        console.log(error);
        return []; //     
    }
}



